// SIGNUP.ts
//---------------------------

import { Component, Input } from '@angular/core';

/**
 * Interface for Signup field
 *
 * @property id (M)
 * @property label (M)
 * @property inputType (M)
 * @property payload (M)
 * @property error (O)
 * @property hint (O)
 * @property classes (O)
 */
interface ISignupField {
  id: string;
  label: string;
  inputType: 'password' | 'text' | 'email';
  payload: any;
  error?: string;
  hint?: string;
  classes?: string;
}

/**
 * Interface for Signup password field (extends ISignupField)
 *
 * @property show (O)
 */
interface ISignupPasswordField extends ISignupField {
  show?: {
    icon: string;
    label: string;
    payload: any;
  }
}

/**
 * Interface for Signup submit
 *
 * @property label (M)
 * @property payload (M)
 * @property error (O)
 * @property classes (O)
 */
interface ISignupSubmit {
  label: string;
  payload: any;
  error?: string;
  classes?: string;
}

/**
 * Interface for Signup footer
 *
 * @property label (M)
 * @property action (M)
 * @property classes (O)
 */
interface ISignupFooter {
  label: string;
  action: {
    label: string;
    payload: any;
  }
  classes?: string;
}

/**
 * Interface for Signup data
 * login/signup/forgotPassword groups
 *
 * @property login (O)
 * @property signup (O)
 * @property forgotPassword (O)
 */
export interface ISignupData {
  login?: {
    email: ISignupField;
    password: ISignupPasswordField;
    forgotPassword?: {
      title: string;
      label: string;
      payload: any;
    };
    submit: ISignupSubmit;
    footer?: ISignupFooter;
  };
  signup?: {
    name: ISignupField;
    email: ISignupField;
    password: ISignupPasswordField;
    rePassword: ISignupPasswordField;
    submit: ISignupSubmit;
    footer?: ISignupFooter;
  };
  forgotPassword?: {
    description: string;
    email: ISignupField;
    submit: ISignupSubmit;
    footer?: ISignupFooter;
  }
}

@Component({
  selector: 'n7-signup',
  templateUrl: './signup.html'
})
export class SignupComponent {
  @Input() data: ISignupData;
  @Input() emit: any;

  onClick(payload){
    if(!this.emit) return;
    this.emit('click', payload);
  }

  onChange(inputPayload, value){
    if(!this.emit) return;
    this.emit('change', { inputPayload, value });
  }

  onInputEnter(inputPayload, value){
    if(!this.emit) return;
    this.emit('enter', { inputPayload, value });
  }

  onFocus(payload){
    if(!this.emit) return;
    this.emit('focus', payload);
  }

  onFocusOut(payload){
    if(!this.emit) return;
    this.emit('focusout', payload);
  }
}